import CategoryComponent from '@/components/category'
import LastComponent from '@/components/lastNews'
import { categoriesFR, categoriesFr } from '@/types'
import { createFileRoute } from '@tanstack/react-router'

export const Route = createFileRoute('/news/fr/$postId/page/$pagenumber')({
  loader: ({ params: { postId, pagenumber } }) => {
    if (categoriesFr.has(postId)) {
      return {
        postId,
        code: categoriesFr.get(decodeURIComponent(postId))?.code,
        pagenumber,
      }
    }
    return {
      postId,
      code: 0,
      pagenumber,
    }
  },
  component: () => {
    const { postId, code, pagenumber } = Route.useLoaderData()

    if (!!code && code < 3)
      return (
        <LastComponent
          categoryId={postId}
          vr="fr"
          code={code}
          page={pagenumber}
        />
      )
    return (
      <CategoryComponent
        categoryId={postId}
        vr="fr"
        code={code as number}
        page={pagenumber}
      />
    )
  },
  head: ({ params: { postId } }) => {
    const title: string = categoriesFR.get(postId)?.name || 'categoriy'

    const description = categoriesFR.get(postId)?.name
    const keywords = categoriesFR.get(postId)?.name
    return {
      meta: [
        { name: 'description', content: description },
        { title: title },
        { name: 'keywords', content: keywords },

        // You can add more meta or override
        //
        { property: 'og:locale', content: 'fr_FR' },
        { property: 'og:type', content: 'article' },
        {
          property: 'og:title',
          content: title,
        },
        {
          property: 'og:description',
          content: categoriesFR.get(postId)?.name,
        },
        {
          property: 'og:url',
          content: 'https://fibladi.com/news/fr/' + postId,
        },

        { property: 'og:site_name', content: 'fibladi' },
        {
          property: 'article:published_time',
          content: '2025-09-09T14:42:20+00:00',
        },
        {
          property: 'og:image',
          content:
            'https://fibladi.com/news/fr/wp-content/uploads/sites/2/2019/10/default-390x260-1.jpg',
        },
        { property: 'og:image:width', content: '895' },
        { property: 'og:image:height', content: '576' },

        {
          property: 'twitter:card',
          content: 'summary',
        },
        {
          property: 'twitter:creator',
          content: '@fibladi',
        },
        {
          property: 'twitter:site',
          content: '@fibladi',
        },
        {
          property: 'twitter:title',
          content: title,
        },
        {
          property: 'twitter:description',
          content: categoriesFR.get(postId)?.name,
        },
      ],
      links: [
        {
          rel: 'canonical',
          href: 'https://fibladi.com/news/' + postId,
        },
      ],
    }
  },
})
